nav {
    background-color: #343b40;
}

a {
    text-decoration: none;
    color: white
}
 a:hover {
    text-decoration: underline;
    color: white
}

.navbar-brand {
    font-size: 35px;
}

.nav-item {
    font-size: 25px;
}

.navbar-nav {
    padding-bottom: 15px;
}

.navbar {
    margin: 0px;
    padding: 0px;
}

#nav-item-1 {
    padding-right: 10px;
}
.jumbotron {
    background-image: url(/static/media/book.b234bb17.jpg);
}

ul {
    list-style-type:none;
    font-weight: bold;
    font-size: 16px;
    padding-left: 25px;
    padding-top: 15px;
}

.row {
    margin-top: 15px;
}

img {
    margin-left: 25px;
    margin-top: 25px;
    padding-bottom: 25px;
    width: 200px;
}
.Btn {
    float: right;
    margin-right: 25px;
}
#form-container {
    border: 1px solid rgba(0,0,0,.125);
    margin-bottom: 25px;
    padding: 25px 25px 55px 25px;
}

button {
    float: right;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.results {
  border: 1px solid rgba(0,0,0,.125);
  margin-bottom: 25px;
  padding: 10px 
}

#saved, #result {
  padding: 15px;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

